:root {
  --clr-soft-red: hsl(7, 99%, 70%);
  --clr-yellow: hsl(51, 100%, 49%);
  --clr-dark-green: hsl(167, 40%, 24%);
  --clr-dark-blue: hsl(198, 62%, 26%);
  --clr-dark-cyan: hsl(168, 34%, 41%);
  --clr-gray-400: hsl(212, 27%, 19%);
  --clr-gray-300: hsl(213, 9%, 39%);
  --clr-gray-200: hsl(232, 10%, 55%);
  --clr-gray-100: hsl(210, 4%, 67%);
  --clr-white: #fff;
  --ff-sans: "Barlow", Arial, Helvetica, sans-serif;
  --ff-serif: "Fraunces", Cochin, Georgia, serif;
  --fs-4xlarge: 3.815rem;
  --fs-3xlarge: 3.052rem;
  --fs-2xlarge: 2.44125rem;
  --fs-xlarge: 1.953125rem;
  --fs-large: 1.5625rem;
  --fs-med: 1.25rem;
  --fs-reg: 1.125rem;
  --fs-small: 0.8rem;
  --fs-xsmall: 0.64rem;
  --gap: 1.25rem;
  --columns: 2;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

body {
  font-family: var(--ff-sans);
  font-weight: 600;
  font-size: var(--fs-reg);
  line-height: 1.6;
  position: relative;
}

html,
body {
  margin: 0px auto;
  width: 100%;
  box-sizing: border-box;
  background: var(--clr-gray-400);
  color: var(--clr-white);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.skip {
  position: absolute;
  left: -9999px;
  z-index: 999;
  padding: 1em;
  background-color: black;
  color: white;
  opacity: 0;
}

.skip:focus {
  left: 50%;
  transform: translateX(-50%);
  opacity: 1;
}

/* prettier-ignore */
h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  line-height: 1.15;
  letter-spacing: -0.022em;
  margin: 0;
  margin-bottom: 1.25rem;
}

h1 {
  font-size: var(--fs-4xlarge);
}

h2 {
  font-size: var(--fs-3xlarge);
}

h3 {
  font-size: var(--fs-2xlarge);
}

h4 {
  font-size: var(--fs-xlarge);
}

h5 {
  font-size: var(--fs-large);
}

h6 {
  font-size: var(--fs-med);
}

p {
  font-size: var(--fs-reg);
  margin-bottom: 1.25rem;
}

.small,
small {
  font-size: var(--fs-small);
}

.xsmall {
  font-size: var(--fs-xsmall);
}

