.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

header {
  position: relative;
  height: 33.625rem;
}
header img {
  max-width: 100%;
}

.container {
  margin: 0 auto;
  padding: 1.25rem;
  width: 100%;
}
@media (min-width: 64em) {
  .container {
    max-width: 69.375rem;
  }
}

.flex {
  display: flex;
  flex-wrap: wrap;
  gap: var(--gap);
}
@media (min-width: 28.125em) {
  .flex__item {
    flex: 0 1 calc(100% / var(--columns) - var(--gap) + var(--gap) / var(--columns));
  }
}
@media (min-width: 48em) {
  .flex__item {
    --columns: 4;
    flex: 0 1 calc(100% / var(--columns) - var(--gap) + var(--gap) / var(--columns));
  }
}

.row {
  display: grid;
  column-gap: 1em;
}
@media (min-width: 64em) {
  .row {
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: 1fr auto;
  }
}

@media (min-width: 64em) {
  .col-fourth {
    grid-column: auto/span 3;
  }
  .col-third {
    grid-column: auto/span 4;
  }
  .col-two-thirds {
    grid-column: auto/span 8;
  }
  .col-half {
    grid-column: auto/span 6;
  }
  .col-three-fourths {
    grid-column: auto/span 9;
  }
  .col-1 {
    grid-column: auto/span 1;
  }
  .col-2 {
    grid-column: auto/span 2;
  }
  .col-3 {
    grid-column: auto/span 3;
  }
  .col-4 {
    grid-column: auto/span 4;
  }
  .col-5 {
    grid-column: auto/span 5;
  }
  .col-6 {
    grid-column: auto/span 6;
  }
  .col-7 {
    grid-column: auto/span 7;
  }
  .col-8 {
    grid-column: auto/span 8;
  }
  .col-9 {
    grid-column: auto/span 9;
  }
  .col-10 {
    grid-column: auto/span 10;
  }
  .col-11 {
    grid-column: auto/span 11;
  }
  [class*=col-start-] {
    grid-row-start: 1;
  }
  .col-start-first {
    grid-column-start: 1;
  }
  .col-start-fourth {
    grid-column-start: 4;
  }
  .col-start-third {
    grid-column-start: 5;
  }
  .col-start-half {
    grid-column-start: 7;
  }
  .col-start-two-thirds {
    grid-column-start: 9;
  }
  .col-start-three-fourths {
    grid-column-start: 10;
  }
